<template>
  <v-row class="dashboard-painel elevation-1">
    <v-col cols="12" sm="4" class="px-0 px-md-4">
      <dashboard-revenues-painel-item
        title="Faturamento Total"
        icon="monetization_on"
        :text="revenues"
      />
    </v-col>
    <v-col cols="12" sm="4" class="px-0 px-md-4">
      <dashboard-revenues-painel-item
        title="Atendimentos"
        icon="today"
        :text="qtdSchedules"
      />
    </v-col>
    <v-col cols="12" sm="4" class="px-0 px-md-4">
      <dashboard-revenues-painel-item
        title="Barbeiros"
        icon="people_alt"
        :text="qtdBarbers"
      />
    </v-col>
  </v-row>
</template>

<script>
import DashboardRevenuesPainelItem from './DashboardRevenuesPainelItem'
import BarbershopMixin from '@/mixins/BarbershopMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import TextMixin from '@/mixins/TextMixin'
export default {
  name: 'DashboardRevenuesPainel',
  components: { DashboardRevenuesPainelItem },
  mixins: [ BarbershopMixin, SnackbarMixin, TextMixin ],
  data: () => ({
    qtdBarbers: 0,
    qtdSchedules: 0,
    revenues: 0
  }),
  mounted () {
    this.getTotalByBarbershop()
  },
  methods: {
    getTotalByBarbershop () {
      const errorMessage = 'Não foi possível buscar os dados da barbearia!'
      this.$axios(`barbershop/${this.barbershop.id}/total`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            this.qtdBarbers = response.data.corpo.barbers
            this.qtdSchedules = response.data.corpo.schedules
            const revenues = Number(response.data.corpo.revenues).toFixed(2)
            this.revenues = this.setMoneyMask(revenues)
          } else this.showSnackbarErro(response.data.corpo)
        } else this.showSnackbarErro(errorMessage)
      }).catch(() => this.showSnackbarErro(errorMessage))
    }
  }
}
</script>

<style lang="scss" scoped>
.dashboard-painel { background: #fff; border-radius: 15px; padding: 20px 15px; }
.dashboard-painel > div { border-left: 1px solid #ccc; }
.dashboard-painel > div:first-child { border: none !important; }
@media all and (max-width: 599px) {
  .dashboard-painel > div { border: none !important; }
}
</style>